<template>
	<div>
		<ul>
			<li v-for='data in datalist' @click='handleclick(data.id,data.name)'>
				<products :data='data'></products>
			</li>
		</ul>
	</div>
</template>

<script>
import axios from "axios";
import products from "@/components/common/new";
import router from "@/router";
export default {
  name: 'xxls',

  data () {
    return {
    }
  },
  components:{
  	products
  },
  mounted(){
  	  	if(!this.datalist.length){
  		  this.$store.dispatch('xxls')
  	}
  },
  computed:{
  	datalist:function(){
  		return this.$store.state.xxlscon?this.$store.state.xxlscon:[]
  	}
  },
  methods:{
  	handleclick(index,name){
  		this.$store.commit('title',name)
  		router.push({name:'detail',params:{id:index}})
  	}
  }
}
</script>

<style lang="scss" scoped>
div{
  float: right;
  width: 80%
}
ul{
  width: 100%
}
li{
  height: 1.1rem;
  width: 100%;
  padding: 0.05rem;
  font-size: 14px;
  position: relative;
  border-bottom: 1px solid #ccc;
    
}


</style>


