import Vue from 'vue'
import Router from 'vue-router'

import Home from "@/components/home.vue";
import Market from "@/components/market.vue";
import Card from "@/components/card.vue";
import My from "@/components/my.vue";
import detail from "@/components/detail.vue";

import Rxxp from "@/components/common/rxxp.vue";
import Yxsg from "@/components/common/yxsg.vue";
import Nnmb from "@/components/common/nnmb.vue";
import Yljs from "@/components/common/yljs.vue";
import Xxls from "@/components/common/xxls.vue";
import Fbss from "@/components/common/fbss.vue";
import Lytw from "@/components/common/lytw.vue";
import Shyp from "@/components/common/shyp.vue";
import Ptdg from "@/components/common/ptdg.vue";




Vue.use(Router)

const router =  new Router({
	made:"hash",
  routes: [
    {
      path: '/home',
      component: Home
    },  {
    	path:"/market",
    	component:Market,
      redirect:'/common/rxxp',
      children:[
                  {
          path:"/common/rxxp",
          component:Rxxp
        },      {
          path:"/common/yxsg",
          component:Yxsg
        },      {
          path:"/common/nnmb",
          component:Nnmb
        },      {
          path:"/common/yljs",
          component:Yljs
        },      {
          path:"/common/xxls",
          component:Xxls
        },      {
          path:"/common/fbss",
          component:Fbss
        },      {
          path:"/common/lytw",
          component:Lytw
        },      {
          path:"/common/shyp",
          component:Shyp
        },      {
          path:"/common/ptdg",
          component:Ptdg
        },
        ]
    },
      {
    	path:"/card",
    	component:Card
    },
      {
    	path:"/my",
    	component:My
    },
      {
        path:"/detail:id", 
        name:'detail',
        component:detail
    },


      {
      	path:"*",
      	redirect:"/home"
      },
  ]
})

export default router;



