<template>
<nav>
	<div id="top">
		<h4>{{title}} ▼ </h4>
	</div>
	<div id="search">
		<img src="../assets/search.png" height="35" width="35">
	</div>
</nav>
</template>

<script>
import {mapState} from 'vuex';
document.documentElement.style.fontSize=window.innerWidth/375*100+"px";

export default {

  name: 'navbar',

  data () {
    return {

    }
  },
  computed:{
  		...mapState(['title'])
  }
}
</script>

<style lang="scss" scoped>
nav{
	height: 0.57rem;
	width: 100%;
	background-color:#FFD600;
	text-align: center;
}
h4{
	line-height: 0.57rem;

}
#search{
	position: absolute;
	right: 0;
	top: 11px;
}
</style>


