import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
import axios from 'axios';


const store = new Vuex.Store({
	state:{
		title:'北京天安门',
		yxsgcon: null,
		yljscon:null,
		xxlscon:null,
		shypcon:null,
		rxxpcon:null,
		ptdgcon:null,
		nnmbcon:null,
		lytwcon:null,
		fbsscon:null,
		carscon:[],
		numbers:null
	},
	actions:{
		yxsg:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
				this.datalist=res.data.data.products[103532]
				store.commit('yxsgs',this.datalist)
			})
		},
		yljs:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
				this.datalist=res.data.data.products[103549]
				store.commit('yljs',this.datalist)
			})
		},
		xxls:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
			  	this.datalist=res.data.data.products[103557]
			  	store.commit('xxls',this.datalist)
			})
		},
		shyp:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
			  	this.datalist=res.data.data.products[103575]
			  	store.commit('shyp',this.datalist)
			})
		},
		rxxp:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
				this.datalist=res.data.data.products[104749]
				store.commit('rxxp',this.datalist)
			})
		},
		ptdg:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
				this.datalist=res.data.data.products[104987]
				store.commit('ptdg',this.datalist)
			})
		},
		nnmb:function(state,payload){
	  	  	axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
	  		this.datalist=res.data.data.products[103536]
				store.commit('nnmb',this.datalist)
			})
		},
		lytw:function(state,payload){
		  	axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
  			this.datalist=res.data.data.products[103569]
				store.commit('lytw',this.datalist)
			})
		},
		fbss:function(state,payload){
			axios.get('/data/getCategoryProduct?asid=5a4b6bacae4c73839&_r=0.0018631898215766274&reflogid=5a4cc1d29c5855431&location_hash=e2bff7W43kU9%2FP%2BVMpe%2Foaf4PMIAR7jGSPbEiycKpprhkaILGnrzE9f96KN9DyMsC7C9IzaMhIkEY%2BPVMKIg&zchtid=15174&bigids=2%2C0').then(res=>{
			this.datalist=res.data.data.products[103541]
				store.commit('fbss',this.datalist)
			})
		}

	},
	mutations:{
		title:function(state,payload){
			state.title=payload
		},
		yxsgs:function(state,payload){
			state.yxsgcon=payload
		},
		yljs:function(state,payload){
			state.yljscon=payload
		},
		xxls:function(state,payload){
			state.xxlscon=payload
		},
		shyp:function(state,payload){
			state.shypcon=payload
		},
		rxxp:function(state,payload){
			state.rxxpcon=payload
		},
		ptdg:function(state,payload){
			state.ptdgcon=payload
		},
		nnmb:function(state,payload){
			state.nnmbcon=payload
		},
		lytw:function(state,payload){
			state.lytwcon=payload
		},
		fbss:function(state,payload){
			state.fbsscon=payload
		},
		cars:function(state,payload){
			for (var i=0; i<=state.carscon.length;i++){
				if(state.carscon.length==0){
					state.carscon.push(payload)
					return
				}else if(payload.id!=state.carscon[i].id){
					for( var j=i; j<state.carscon.length;j++){
						if(payload.id==state.carscon[j].id){
							return
						}
					}
					state.carscon.push(payload)
				}else{
					return
				}
			}				
		},
		numbersless:function(state,payload){
			console.log(state.carscon.length)
			console.log('id',payload)
			for(var i=0;i<state.carscon.length;i++){
				if(state.carscon[i].id==payload){
					console.log(state.carscon)
					console.log(state.carscon[i].numbers)
					state.carscon[i].numbers--
				}
			}
		}
	}
});
export default store;


