<template>
	<div class='container' v-show='isShow'>
		<cheader></cheader>
		<presoninfo :myshow='isShow'></presoninfo>
		<div class="super">
			<div class="group-name"><span></span><div>闪送超市</div></div>
			<span class="btn">凑单专区</span>
			<p class="theme-grayfont group-proptext ui-ellipsis ">¥0起送，22:00前满¥30免运费</p>
		</div>
		<timelist></timelist>
		<notice></notice>
		<div v-for='data in datalist'>
			<products :data='data'></products>
		</div>
		
		<table class="allcheck">
			<tbody>
				<tr>
					<td width="20%"><input type="checkbox" name="" class="check">全选
					</td>
					<td width="50%">共:<span>￥{{100}}</span></td>
					<td width="30%">
						<div>选好了</div>
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
import cheader from './common/cardhead'
import presoninfo from './common/presoninfo'
import timelist from './common/timelist'
import notice from './common/notice';
import products from './common/products'
export default {

  name: 'card',

  data () {
    return {
    	isShow:true,
    	datalist:[],
    	count:0,
    	cars:[],
    	checked: true,
    	// numb:true
    }
  },
  components:{
  	cheader,
  	presoninfo,
  	timelist,
  	notice,
  	products
  },
  mounted(){
  	// console.log(this.$store.state.carscon)
  	if(this.$store.state.carscon){
  		this.datalist=this.$store.state.carscon
  	};
  	console.log(this.datalist)
  	// this.numbers=this.$store.state.carscon
  },
}
</script>

<style lang="scss" scoped>
	.container{
		background:#efefef;
		margin-bottom:100px;
	}
	.all{
		float: right;
		width: 100%;
		border-bottom: solid 1px #cecece;
		padding:10px;
		table{
			width: 100%;
			text-align: center;
			div.group{
				text-align: left
			}
			.box{
				text-align: right;
				div{
					display: inline-block;
					width: 30px;
					height: 30px;
					line-height: 30px;
					text-align: center;
					border-radius: 50%;
					border: 1px solid #ff4300;
				}
			}
			img{
				width:100%
			}
		}
	}
	.allcheck{
		width: 100%;
		height: 60px;
		text-align: center;
		font-size: 16px;
		color: #333;
		span{
			color:#ff4300
		}
		div{
			width: 100%;
			height: 60px;
			background: #ffd600;
			line-height: 60px
		}
	}
	.super{
		padding:10px;
		background:white;
		position: relative;
		color: #999;
		border-bottom: 1px solid #cecece;
		div{
			height: 36px;
			padding-left: 10px;
			line-height:36px;
			position:relative;
			span{
				position: absolute;
				top:8px;
				border-radius:3px;
				width:5px;
				display: inline-block;
				height:20px;
				background:#ffd600
			}
		}
		.btn{
			position:absolute;
			top:10px;
			right: 20px;
			width:85px;
			height: 30px;
			border:1px solid #e64f1a;
			border-radius: 20px;
			line-height:30px;
			text-align: center;
			color:#e64f1a
		}
		p{
			padding-left: 10px;
		}
		
	}
</style>


