<template>
	<div class="all" v-show='isShow'>			
		<table>
			<tbody>
				<tr>
					<td width="20%"><input type="checkbox" name="" class="check">
					</td>
					<td width="20%"><img :src="data.app_mimg"></td>
					<td width="60%">
						<div class="group">{{data.name}}</div>
						<div class="group" width="50px"><span>￥{{data.price}}</span></div>
						<div class="box">
							<div class="less" @click.stop='handleclickless(data)'>-</div>
							<span>{{data.numbers}}</span>
							<div class="add" @click.stop='handleclickadd(data)'>+</div>
						</div>
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
export default {

  name: 'products',
  props:['data'],
  number:0,
  data () {
    return {
    	isShow:true
    }
  },
  
  mounted(){
  	this.number=this.data.numbers;
  	console.log(this.number)
  	if(this.data.numbers<=0){
  		this.isShow=false
  	}
  },
   methods:{
  	handleclickless(data){
  		
  		// this.isShow=!isShow;
  		this.number--;
  		this.$store.commit('numbersless',data.id)
  		if(data.numbers<=0){
  			console.log(this.isShow)
  			this.isShow=false
  		}
  	},
  	handleclickadd(data){

  	}
  }
}
</script>

<style lang="scss" scoped>
.all{
	float: right;
	width: 100%;
	border-bottom: solid 1px #cecece;
	padding:10px;
	table{
		width: 100%;
		text-align: center;
		div.group{
			text-align: left
		}
		.box{
			text-align: right;
			div{
				display: inline-block;
				width: 0.3rem;
				height: 0.3rem;
				line-height: 0.3rem;
				text-align: center;
				border-radius: 50%;
				border: 1px solid #ff4300;
			}
		}
		img{
			width:100%
		}
	}
}
.allcheck{
	width: 100%;
	height: 0.6rem;
	text-align: center;
	font-size: 16px;
	color: #333;
	span{
		color:#ff4300
	}
	div{
		width: 100%;
		height: 0.6rem;
		background: #ffd600;
		line-height: 0.6rem
	}
}
.super{
	padding:0.1rem;
	background:white;
	position: relative;
	color: #999;
	border-bottom: 1px solid #cecece;
	div{
		height: 0.36rem;
		padding-left: 10px;
		line-height:0.36rem;
		position:relative;
		span{
			position: absolute;
			top:0.08rem;
			border-radius:0.03rem;
			width:0.05rem;
			display: inline-block;
			height:0.2rem;
			background:#ffd600
		}
	}
	.btn{
		position:absolute;
		top:0.10rem;
		right: 0.2rem;
		width:0.85rem;
		height: 0.3rem;
		border:1px solid #e64f1a;
		border-radius:0.2rem;
		line-height:0.3rem;
		text-align: center;
		color:#e64f1a
	}
	p{
		padding-left: 0.10rem;
	}
	
}
</style>


