 <template>
	<div class="all">
		<img :src="data.app_mimg">
		<div class="lidiv">
			<p class="p1">{{data.name}}</p>
			<p class="p2">精选</p>
			<p class="p3">{{data.specifics}}</p>
			<p class="p4">
				<span>￥{{data.price}}</span>
				<span>￥{{data.market_price}}</span>
			</p>
			<div class="box">
				<div class="less" @click.stop='handleclickless()' v-show='isshow'>-</div>
				<span v-show='isshow'>{{count}}</span>
				<div class="add" @click.stop='handleclickadd(data)'>+</div>
			</div>
		</div>
	</div>
</template>

<script>
import router from "@/router";
import { Toast } from 'mint-ui';
export default {
  name: 'new',
    data () {
	    return {
	    	datalist:[],
	    	count:0,
	    	isshow:false,
	    	cars:[],

	    }
  },
  methods:{
  	handleclickadd(data){
  		this.count++;
  		this.isshow=true;
  		if(this.count>data.number){
  			this.count=data.number;
  			Toast(`${data.name}库存不足`);
  		};
  		data.numbers=this.count
  		this.$store.commit('cars',data)

  	},
  	handleclickless(){
  		this.count--;
  		if(this.count==0){
  			this.isshow=false
  		}
  	}
  },
  props:['data']
}
</script>
<style lang="scss" scoped>
.all{
	float: right;
	width: 100%;
	height:100%;
	img{
		width: 35%;
		height: 100%;
		float: left;
	}
	div{
		float: right;
		width: 65%;
		p{
			width: 100%
		}
		.p2{
			width: 20%;
			height:5%;
			border: 1px solid #f00;
			font-size:12px;
			color: #f00;
			text-align: center;
			border-radius: 5px;
			margin-top: 5px;
			margin-bottom: 5px
		}
		.p3{
			color:#ccc;
			height:0.24rem;
			line-height: 0.24rem;
			font-size:13px
		}
		.p4{
			span:nth-child(1){
				color:#f00;
				font-size:14px;
			}
			span:nth-child(2){
				color:#ccc;
				font-size:14px;
				text-decoration: line-through
			}
		}
		.box{
		width:0.90rem;
		height:0.30rem;
		position:absolute;
		bottom: 20px;
		right:20px;
		text-align: center;
		line-height:30px;
	  .add{
		width: 0.30rem;
		height:0.30rem;
		border-radius: 50%;
		color:#f00;
		border:1px solid #ccc;
		text-align: center;
		line-height:0.30rem;
		font-weight: 600;
		float:right;
		z-index: 999
	}
	.less{
		width: 0.30rem;
		height:0.30rem;
		border-radius: 50%;
		color:#f00;
		border:1px solid #ccc;
		text-align: center;
		line-height:0.30rem;
		font-weight: 600;
		float:left;
	}

	}
	}

}


</style>


