<template>
	<html v-html="filminfo">
		
	</html>
</template>

<script>
import axios from "axios";
export default {

  name: 'detail',

  data () {
    return {
filminfo :null
    }
  },
   mounted () {



  	axios.get(`/show/productDetail?asid=5a4b6bacae4c73839&_r=0.36103692649909935&reflogid=5a4ce05332d555766&location_hash=b9ef6bHBHLuPKwWl1NnYo7EJN%2FCImi2odNuCt1bQMcbMdb6%2BWsyXH4xVk%2BaLduCjmb%2FOmJOplop2hldpUSCA&from=vv&id=${this.$route.params.id}&shopId=15174&bigids=2%2C0&simulate_mobile=`).then(res=>{
  		this.filminfo= res.data
  	})
  }

}
// http://m.beequick.cn/show/productDetail?asid=5a4b6bacae4c73839&_r=0.36103692649909935&reflogid=5a4ce05332d555766&location_hash=b9ef6bHBHLuPKwWl1NnYo7EJN%2FCImi2odNuCt1bQMcbMdb6%2BWsyXH4xVk%2BaLduCjmb%2FOmJOplop2hldpUSCA&from=vv&id=126065&shopId=15174&bigids=2%2C0&simulate_mobile=
</script>

<style lang="css" >
img{
	width: 100%;
	height: 100%
}
#search{
	display: none;
}
.block{
	border-bottom: 5px solid #eee;
	padding-bottom: 30px
}
.product-name{
	font-size: 30px;
	box-sizing:border-box;
	padding: 0 20px

}
.product-shopping{
	box-sizing:border-box;
	padding: 0 20px;
	text-align: center;
	font-size: 24px;
	margin-top: 20px
}
.product-price{
	color: red;
	font-weight: 600;
}
.shopping-jump{
	display: none;
}
.title{
	text-align: center;
	color: #e0bd6a;
	height: 50px;
	line-height: 50px

}
.property-item{
	box-sizing:border-box;
	padding: 0 20px;
	height: 30px;
	line-height: 30px
}
.ui-hidden{
	visibility: hidden;
}
.leading-word{
	width: 50px;
	text-align: center;
}
.content-word{
	padding-left: 30px
}
.detail-pull{
	text-align: center;
	height:60px;
	line-height: 60px
}
#bannerContainer{
	background-image: url(/static/li.png) ;
	background-repeat: no-repeat;
	background-position: center;
	height: 300px;

	
}

</style>


